body{
    background-color: #1d1d1d;
    color: #fff;
    text-align: center;
    font-size: 20px;
    font-family: Arial, Helvetica, sans-serif;
    padding-top: 5%;
    word-wrap: break-word;
}

.logo {
    width: 600px;
    height: 350px;
}

.App {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.walletInfo {
   
    width: 500px;
}

.Block {
    border: 1px solid #fff;
    padding: 5%;
    margin: 2%;
}

.Transaction{
    padding: 5%;
}

.buttonShow{
    background-color: #e71515;
}

a, a:hover {
    color: #e66;
    text-decoration: underline;
}

.ConductTransaction, .TransactionPool {
    margin: 20%;
}